.intro-container {
  display: flex;
  flex: 1 1;
  padding: 2% 5% 3% 0;
}
.intro-content {
  display: flex;
  flex: 1 1;
  flex-direction: column;
}
.greeting-sentence {
  padding: 3% 0 7% 0;
}
.greeting {
  font-size: 28px;
  font-weight: bolder;
}
img {
  width: 100%;
  border-radius: 0.5em;
  align-self: center;
}
.detail-sentence {
  padding: 10% 0;
}
.font18 {
  font-size: 18px;
}

