  font-size: 19px;
  background-color: #f8f9fa;
  color: #212529;
  padding: 7px 15px;
  border-radius: 0.5em;
  margin: 1%;
}

.title {
  font-size: 26px;
  font-weight: bolder;
  padding-bottom: 2%;
  border-bottom: 1px solid #ced4da;
}

.set {
  padding: 2% 0;
}

.set-name {
  padding-left: 1%;
