.header-container {
  position: fixed;
  width: 95%;
  height: 15%;
  z-index: 1;
  background-color: #ffffff;
  display: flex;
  flex: 1 1;
  padding-bottom: 1%;
  align-items: center;
}

.header-left {
  width: 50%;
}

.name {
  text-decoration: none;
  color: #000000;
  font-size: 40px;
  font-weight: 900;
}

p {
  color: #212529;
  font-size: 16px;
}

.header-right {
  width: 50%;
  text-align: right;
  font-weight: bold;
}

.hide {
  text-decoration: none;
}

.link {
  margin-right: 3%;
  padding-bottom: 1%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #212529;
  font-size: 20px;
  text-decoration: none;
}
.underLine {
  border-bottom: 1px #212529 solid;
}

